import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./components/Home";
import Contact from "./components/Contact";
import Error404 from "./components/Error404";
import SoftwareDevPage from "./components/SoftwareDevPage";
import DigitalMarktPage from "./components/DigitalMarktPage";

function App() {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/contact" component={Contact} />
        <Route exact path="/digital" component={DigitalMarktPage} />
        <Route exact path="/software" component={SoftwareDevPage} />
        <Route Component={Error404} />
      </Switch>
    </Router>
  );
}

export default App;
