import React, { Component } from "react";
import Nav from "./Nav";
import Footer from "./Footer";

export default class Contact extends Component {
  render() {
    return (
      <div>
        <Nav />
        <div className="container mb-5" style={{ marginTop: "10em" }}>
          <p className="h3">CONTACT INFORMATION</p>
          <div className="row ">
            <div className="col-xl-6 mb-5">
              <form>
                <div className="form-control-group">
                  <label>Full Name</label>
                  <input
                    type="text"
                    className="form-control mb-3"
                    placeholder="Ener Stuff here"
                  />
                  <label>Subject</label>
                  <input
                    type="text"
                    className="form-control mb-3"
                    placeholder="Ener Stuff here"
                  />
                  <label>Message</label>
                  <textarea
                    type="text"
                    className="form-control mb-3"
                    placeholder="Ener Stuff here"
                  />
                  <button type="submit" className="btn btn-success shadow-lg">
                    SEND MESSAGE
                  </button>
                </div>
              </form>
            </div>

            <div className="col-xl-6 mb-5">
              <p>Email: harcourtinc@gmail.com</p>
              <p>Phone: +234 807 533 9205</p>
              <p>Address: No.14 Beggar Road. Lagos State</p>
              <p>HAVING TROUBLE LOCATING US?</p>
              <iframe
                src="http://www.facebook.com"
                className="border-0"
              ></iframe>
              <p>OUR MEDIA HANDLES!</p>
              <div>
                <i className="fa fa-facebook h3 p-2"></i>
                <i className="fa fa-twitter h3 p-2"></i>
                <i className="fa fa-instagram h3 p-2"></i>
                <i className="fa fa-youtube h3 p-2"></i>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
